import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import firebase from 'firebase';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {ChartDataSets, ChartType} from 'chart.js';
import {Color, Label} from 'ng2-charts';

@Component({
    selector: 'app-user-detail',
    templateUrl: './user-detail.component.html',
    styleUrls: ['./user-detail.component.scss']
})
export class UserDetailComponent implements OnInit, AfterViewInit {

    constructor(
        private route: ActivatedRoute,
    ) {
    }

    public user: UserObj[] = [];
    private avgLatitude = 0;
    private avgLongitude = 0;
    public initLatitude = 0;
    public initLongitude = 0;
    public get = false;
    public displayedColumns: string[] = ['device_id', 'time', 'soil', 'temperature', 'humidity', 'RSSI_WiFi'];
    public dataSource = new MatTableDataSource<DataObj>();
    public lineChartData: ChartDataSets[] = [
        {data: [], label: '土壌'},
        {data: [], label: '温度'},
        {data: [], label: '湿度'}
    ];
    public lineChartLabels: Label[] = [];
    public lineChartOptions = {
        responsive: true,
    };
    public lineChartColors: Color[] = [
        {
            borderColor: 'black',
            backgroundColor: 'rgba(255,0,255,0.28)',
        },
    ];
    public lineChartLegend = true; // グラフの属性ラベル
    public lineChartPlugins = [];
    public lineChartType: ChartType = 'line';

    @ViewChild(MatPaginator)
    paginator!: MatPaginator;

    // @ViewChild(MatPaginator) dataSource: MatTableDataSource<DataObj>;

    ngOnInit(): void {
        const database = firebase.database();
        this.dataSource.data = [];

        console.log(database.ref('users/' + this.route.snapshot.paramMap.get('id') + '/position')
            .orderByChild('time')
            .once('value', (obj) => {
                const key = obj.key;
                const data = obj.val();

                console.log(key);
                console.log(data);

                for (const item of Object.entries(data)) {
                    let comment = 'no data';
                    console.log(item);
                    const tmp = item[1] as UserObj;
                    if (tmp.data) {
                        const tmpData: DataObj[] = [];
                        for (const oneData of Object.entries(tmp.data)) {
                            console.log(oneData);
                            tmpData.push(oneData[1]);
                        }

                        // 時間を降順に並び替え
                        // #2 Issue
                        tmpData.sort((n1, n2) => {
                            if (n1.time > n2.time) {
                                return -1;
                            }

                            if (n1.time < n2.time) {
                                return 1;
                            }
                            return 0;
                        });
                        tmp.data = tmpData;

                        comment = 'deviceID: ' + tmpData[0].device_id + '\nsoil: ' + tmpData[0].soil.toString() + '\n';
                        console.log('OK');
                    }
                    tmp.comment = comment;
                    this.user.push(tmp);
                    // console.log(tmp);
                    // console.log(tmp.latitude);
                    this.initLatitude += tmp.latitude;
                    this.initLongitude += tmp.longitude;
                }

                this.get = true;

                console.log(this.initLatitude, this.initLongitude);

                // 経度と緯度の平均を算出
                this.initLatitude = this.initLatitude / this.user.length;
                this.initLongitude = this.initLongitude / this.user.length;

                this.avgLatitude = this.initLatitude;
                this.avgLongitude = this.initLongitude;

                console.log(this.user);
            }));
    }

    ngAfterViewInit(): void {
        this.dataSource.paginator = this.paginator;
    }

    init(): void {
        this.initLatitude = this.avgLatitude;
        this.initLongitude = this.avgLongitude;
    }

    detailTable(i: number): void {
        // dataが存在する場合は、dataSourceにデータを代入し存在しない場合は空を挿入する。
        if (this.user[i].data) {
            this.dataSource.data = this.user[i].data;
            const soil: number[] = [];
            const temperature: number[] = [];
            const humidity: number[] = [];
            const time: string[] = [];

            for (const one of this.user[i].data) {
                soil.push(one.soil);
                temperature.push(one.temperature);
                humidity.push(one.humidity);
                time.push(one.time);
            }

            this.lineChartData = [
                {data: soil, label: '土壌'},
                {data: temperature, label: '温度'},
                {data: humidity, label: '湿度'}
            ];
            this.lineChartLabels = time;
        } else {
            this.dataSource.data = [];
            this.lineChartData = [];
            this.lineChartLabels = [];
        }
    }
}

interface UserObj {
    created_at: string;
    updated_at: string;
    id: string;
    latitude: number;
    longitude: number;
    data: DataObj[];
    comment: string;
}

interface DataObj {
    'RSSI_920': number;
    'RSSI_WiFi': number;
    'RSSI_ZigBee': number;
    'humidity': number;
    'temperature': number;
    'Res0': string;
    'Res1': string;
    'Res2': string;
    'Res3': string;
    'device_id': string;
    'id': string;
    'soil': number;
    'time': string;
}
